<template>
  <view class="admin-order-list">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="nav-bar">
      <view class="nav-back" @click="goBack">
        <text class="iconfont">‚Äπ</text>
      </view>
      <view class="nav-title">ËÆ¢ÂçïÁÆ°ÁêÜ</view>
      <view class="nav-placeholder"></view>
    </view>

    <!-- Á≠õÈÄâÊù°‰ª∂Âå∫Âüü -->
    <view class="filter-section">
      <view class="filter-row">
        <view class="filter-item">
          <text class="filter-label">ËÆ¢ÂçïÁä∂ÊÄÅ</text>
          <picker mode="selector" :range="statusOptions" range-key="label"
                  :value="statusIndex" @change="onStatusChange">
            <view class="picker-value">
              {{ statusOptions[statusIndex].label }}
              <text class="arrow">‚ñº</text>
            </view>
          </picker>
        </view>

        <view class="filter-item">
          <text class="filter-label">ËÆ¢ÂçïÁ±ªÂûã</text>
          <picker mode="selector" :range="typeOptions" range-key="label"
                  :value="typeIndex" @change="onTypeChange">
            <view class="picker-value">
              {{ typeOptions[typeIndex].label }}
              <text class="arrow">‚ñº</text>
            </view>
          </picker>
        </view>
      </view>

      <view class="filter-row">
        <view class="filter-item">
          <text class="filter-label">ÂºÇÂ∏∏Á±ªÂûã</text>
          <picker mode="selector" :range="abnormalOptions" range-key="label"
                  :value="abnormalIndex" @change="onAbnormalChange">
            <view class="picker-value">
              {{ abnormalOptions[abnormalIndex].label }}
              <text class="arrow">‚ñº</text>
            </view>
          </picker>
        </view>
      </view>

      <view class="filter-row">
        <view class="filter-item full-width">
          <text class="filter-label">Êó∂Èó¥ËåÉÂõ¥</text>
          <view class="date-picker-group">
            <picker mode="date" :value="startTime" @change="onStartTimeChange">
              <view class="date-picker">
                {{ startTime || 'ÂºÄÂßãÊó•Êúü' }}
              </view>
            </picker>
            <text class="date-separator">Ëá≥</text>
            <picker mode="date" :value="endTime" @change="onEndTimeChange">
              <view class="date-picker">
                {{ endTime || 'ÁªìÊùüÊó•Êúü' }}
              </view>
            </picker>
          </view>
        </view>
      </view>

      <view class="filter-row search-row">
        <view class="search-box">
          <input class="search-input" v-model="keyword"
                 placeholder="ÊêúÁ¥¢ËÆ¢ÂçïÂè∑/Áî®Êà∑/È™ëÊâã/ÂïÜÂìÅ" />
          <button class="search-btn" @click="handleSearch">ÊêúÁ¥¢</button>
        </view>
        <button class="reset-btn" @click="handleReset">ÈáçÁΩÆ</button>
      </view>
    </view>

    <!-- ËÆ¢ÂçïÂàóË°® -->
    <view class="order-list-section">
      <view class="section-header">
        <text class="section-title">ËÆ¢ÂçïÂàóË°®</text>
        <text class="section-count">ÂÖ± {{ total }} Êù°</text>
      </view>

      <view class="order-list" v-if="orderList.length > 0">
        <view v-for="order in orderList" :key="order.id"
              class="order-item"
              @click="goToDetail(order.id)">
          <!-- ËÆ¢ÂçïÂ§¥ÈÉ® -->
          <view class="order-header">
            <text class="order-no">ËÆ¢ÂçïÂè∑Ôºö{{ order.orderNo }}</text>
            <view class="order-status-badge"
                  :class="'status-' + order.status">
              {{ getStatusText(order.status) }}
            </view>
          </view>

          <!-- ËÆ¢ÂçïÂÜÖÂÆπ -->
          <view class="order-content">
            <view class="content-row">
              <text class="label">ÊúçÂä°Á±ªÂûãÔºö</text>
              <text class="value">{{ getOrderTypeText(order.type) }}</text>
            </view>
            <view class="content-row">
              <text class="label">ÂïÜÂìÅÊèèËø∞Ôºö</text>
              <text class="value">{{ order.goodsDesc }}</text>
            </view>
            <view class="content-row">
              <text class="label">Áî®Êà∑‰ø°ÊÅØÔºö</text>
              <text class="value">{{ order.userNickname }}</text>
            </view>
            <view class="content-row" v-if="order.runnerNickname">
              <text class="label">È™ëÊâã‰ø°ÊÅØÔºö</text>
              <text class="value">{{ order.runnerNickname }}</text>
            </view>
            <view class="content-row">
              <text class="label">ÂàõÂª∫Êó∂Èó¥Ôºö</text>
              <text class="value">{{ formatTime(order.createTime) }}</text>
            </view>
          </view>

          <!-- ËÆ¢ÂçïÂ∫ïÈÉ® -->
          <view class="order-footer">
            <view class="order-amount">
              <text class="amount-label">ËÆ¢ÂçïÈáëÈ¢ùÔºö</text>
              <text class="amount-value">¬•{{ order.totalFee }}</text>
            </view>
            <view class="order-action">
              <text class="action-text">Êü•ÁúãËØ¶ÊÉÖ ‚Ä∫</text>
            </view>
          </view>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-else>
        <text class="empty-icon">üìã</text>
        <text class="empty-text">ÊöÇÊó†ËÆ¢ÂçïÊï∞ÊçÆ</text>
      </view>

      <!-- Âä†ËΩΩÊõ¥Â§ö -->
      <view class="load-more" v-if="orderList.length > 0 && hasMore">
        <text class="load-more-text">{{ loading ? 'Âä†ËΩΩ‰∏≠...' : '‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö' }}</text>
      </view>

      <view class="no-more" v-if="orderList.length > 0 && !hasMore">
        <text class="no-more-text">Ê≤°ÊúâÊõ¥Â§ö‰∫Ü</text>
      </view>
    </view>
  </view>
</template>

<script>
import { getAdminOrderList } from '@/api/admin.js'

export default {
  data() {
    return {
      // Á≠õÈÄâÊù°‰ª∂
      statusOptions: [
        { label: 'ÂÖ®ÈÉ®Áä∂ÊÄÅ', value: null },
        { label: 'ÂæÖÊîØ‰ªò', value: 0 },
        { label: 'ÂæÖÊé•Âçï', value: 1 },
        { label: 'ÈÖçÈÄÅ‰∏≠', value: 2 },
        { label: 'Â∑≤ÂÆåÊàê', value: 3 },
        { label: 'Â∑≤ÂèñÊ∂à', value: 4 },
        { label: 'ÈÄÄÊ¨æ‰∏≠', value: 5 }
      ],
      statusIndex: 0,
      statusValue: null,

      typeOptions: [
        { label: 'ÂÖ®ÈÉ®Á±ªÂûã', value: '' },
        { label: 'Â∏Æ‰π∞', value: 'buy' },
        { label: 'Â∏ÆÈÄÅ', value: 'send' },
        { label: 'Â∏ÆÂèñ', value: 'pick' },
        { label: 'ÂÖ®ËÉΩ', value: 'all' }
      ],
      typeIndex: 0,
      typeValue: '',

      abnormalOptions: [
        { label: 'ÂÖ®ÈÉ®ËÆ¢Âçï', value: '' },
        { label: 'Ë∂ÖÊó∂Êú™Êé•Âçï', value: 'timeout_not_taken' },
        { label: 'ÈÖçÈÄÅË∂ÖÊó∂', value: 'timeout_delivery' },
        { label: 'ÊîØ‰ªòÂºÇÂ∏∏', value: 'abnormal_payment' }
      ],
      abnormalIndex: 0,
      abnormalValue: '',

      startTime: '',
      endTime: '',
      keyword: '',

      // ËÆ¢ÂçïÂàóË°®
      orderList: [],
      total: 0,
      current: 1,
      size: 10,
      loading: false,
      hasMore: true
    }
  },

  onLoad() {
    // Âä†ËΩΩËÆ¢ÂçïÂàóË°®
    this.loadOrderList()
  },

  // ‰∏ãÊãâÂà∑Êñ∞
  onPullDownRefresh() {
    this.current = 1
    this.loadOrderList(() => {
      uni.stopPullDownRefresh()
    })
  },

  // ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
  onReachBottom() {
    if (this.hasMore && !this.loading) {
      this.current++
      this.loadOrderList()
    }
  },

  methods: {
    /**
     * Âä†ËΩΩËÆ¢ÂçïÂàóË°®
     */
    async loadOrderList(callback) {
      if (this.loading) return

      try {
        this.loading = true

        const params = {
          status: this.statusValue,
          orderType: this.typeValue,
          abnormalType: this.abnormalValue,
          startTime: this.startTime,
          endTime: this.endTime,
          keyword: this.keyword,
          current: this.current,
          size: this.size
        }

        console.log('üìã Êü•ËØ¢ËÆ¢ÂçïÂàóË°®ÔºåÂèÇÊï∞Ôºö', params)

        const res = await getAdminOrderList(params)
        console.log('üìã ËÆ¢ÂçïÂàóË°®ÂìçÂ∫îÔºö', res)

        if (res.code === 200) {
          const { records, total } = res.data

          if (this.current === 1) {
            this.orderList = records
          } else {
            this.orderList = [...this.orderList, ...records]
          }

          this.total = total
          this.hasMore = this.orderList.length < total
        } else {
          uni.showToast({
            title: res.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩËÆ¢ÂçïÂàóË°®Â§±Ë¥•Ôºö', error)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
        callback && callback()
      }
    },

    /**
     * ÊêúÁ¥¢
     */
    handleSearch() {
      this.current = 1
      this.loadOrderList()
    },

    /**
     * ÈáçÁΩÆÁ≠õÈÄâÊù°‰ª∂
     */
    handleReset() {
      this.statusIndex = 0
      this.statusValue = null
      this.typeIndex = 0
      this.typeValue = ''
      this.abnormalIndex = 0
      this.abnormalValue = ''
      this.startTime = ''
      this.endTime = ''
      this.keyword = ''
      this.current = 1
      this.loadOrderList()
    },

    /**
     * ËÆ¢ÂçïÁä∂ÊÄÅÂèòÊõ¥
     */
    onStatusChange(e) {
      this.statusIndex = e.detail.value
      this.statusValue = this.statusOptions[e.detail.value].value
    },

    /**
     * ËÆ¢ÂçïÁ±ªÂûãÂèòÊõ¥
     */
    onTypeChange(e) {
      this.typeIndex = e.detail.value
      this.typeValue = this.typeOptions[e.detail.value].value
    },

    /**
     * ÂºÇÂ∏∏Á±ªÂûãÂèòÊõ¥
     */
    onAbnormalChange(e) {
      this.abnormalIndex = e.detail.value
      this.abnormalValue = this.abnormalOptions[e.detail.value].value
    },

    /**
     * ÂºÄÂßãÊó∂Èó¥ÂèòÊõ¥
     */
    onStartTimeChange(e) {
      this.startTime = e.detail.value
    },

    /**
     * ÁªìÊùüÊó∂Èó¥ÂèòÊõ¥
     */
    onEndTimeChange(e) {
      this.endTime = e.detail.value
    },

    /**
     * Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖ
     */
    goToDetail(orderId) {
      uni.navigateTo({
        url: `/pages/admin/order-detail?id=${orderId}`
      })
    },

    /**
     * Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊñáÊú¨
     */
    getStatusText(status) {
      const statusMap = {
        0: 'ÂæÖÊîØ‰ªò',
        1: 'ÂæÖÊé•Âçï',
        2: 'ÈÖçÈÄÅ‰∏≠',
        3: 'Â∑≤ÂÆåÊàê',
        4: 'Â∑≤ÂèñÊ∂à',
        5: 'ÈÄÄÊ¨æ‰∏≠'
      }
      return statusMap[status] || 'Êú™Áü•'
    },

    /**
     * Ëé∑ÂèñËÆ¢ÂçïÁ±ªÂûãÊñáÊú¨
     */
    getOrderTypeText(type) {
      const typeMap = {
        'buy': 'Â∏Æ‰π∞',
        'send': 'Â∏ÆÈÄÅ',
        'pick': 'Â∏ÆÂèñ',
        'all': 'ÂÖ®ËÉΩ'
      }
      return typeMap[type] || type
    },

    /**
     * Ê†ºÂºèÂåñÊó∂Èó¥
     */
    formatTime(time) {
      if (!time) return '-'
      const date = new Date(time)
      const month = (date.getMonth() + 1).toString().padStart(2, '0')
      const day = date.getDate().toString().padStart(2, '0')
      const hour = date.getHours().toString().padStart(2, '0')
      const minute = date.getMinutes().toString().padStart(2, '0')
      return `${month}-${day} ${hour}:${minute}`
    },

    /**
     * ËøîÂõû‰∏ä‰∏ÄÈ°µ
     */
    goBack() {
      uni.navigateBack()
    }
  }
}
</script>

<style lang="scss" scoped>
