  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 44px;
  padding: 0 15px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.nav-back {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: #333;
}

.nav-title {
  font-size: 17px;
  font-weight: 600;
  color: #333;
}

.nav-placeholder {
  width: 40px;
}

/* Á≠õÈÄâÊù°‰ª∂Âå∫Âüü */
.filter-section {
  margin-top: 54px;
  background-color: #fff;
  padding: 15px;
  border-bottom: 1px solid #eee;
}

.filter-row {
  display: flex;
  margin-bottom: 12px;
  gap: 10px;
}

