  margin-bottom: 0;
}

.filter-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.filter-item.full-width {
  flex: none;
  width: 100%;
}

.filter-label {
  font-size: 13px;
  color: #666;
  font-weight: 500;
}

.picker-value {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  background-color: #f5f7fa;
  border-radius: 6px;
  font-size: 14px;
  color: #333;
}

.arrow {
  font-size: 12px;
  color: #999;
}

.date-picker-group {
  display: flex;
  align-items: center;
  gap: 10px;
}

.date-picker {
  flex: 1;
  padding: 10px 12px;
  background-color: #f5f7fa;
