  font-size: 14px;
}

.search-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  font-size: 14px;
  border-radius: 6px;
  border: none;
}

.reset-btn {
  padding: 10px 20px;
  background-color: #f5f7fa;
  color: #666;
  font-size: 14px;
  border-radius: 6px;
  border: none;
}

/* ËÆ¢ÂçïÂàóË°®Âå∫Âüü */
.order-list-section {
  padding: 15px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.section-title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.section-count {
  font-size: 13px;
  color: #999;
}

.order-item {
  background-color: #fff;
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 12px;
  border-bottom: 1px solid #f5f5f5;
  margin-bottom: 12px;
}

.order-no {
  font-size: 13px;
  color: #666;
}

.order-status-badge {
  padding: 4px 10px;
