}

.order-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 12px;
}

.content-row {
  display: flex;
  font-size: 14px;
}

.label {
  color: #666;
  min-width: 80px;
}

.value {
  flex: 1;
  color: #333;
  font-weight: 500;
}

.order-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  border-top: 1px solid #f5f5f5;
}

.order-amount {
  display: flex;
  align-items: baseline;
}

.amount-label {
  font-size: 13px;
  color: #666;
}

.amount-value {
  font-size: 18px;
  font-weight: bold;
  color: #ff4d4f;
  margin-left: 4px;
}

.order-action {
  font-size: 14px;
  color: #667eea;
}

.action-text {
  color: #667eea;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0;
}

.empty-icon {
  font-size: 64px;
